<template>
  <div class="base-video" ref="wrap">
    <!--<div class="loading" :class="{hidden: !loading}">-->
      <!--<img src="@img/nav/football_unselected.png" v-if="ball===1">-->
      <!--<img src="@img/nav/basketball_unselected.png" v-else>-->
      <!--<span>...</span>-->
    <!--</div>-->
  </div>
</template>

<script>
import Hls from 'hls.js'
import DPlayer from 'dplayer'
import logo from '@img/nav/logo.png'
export default {
  name: 'BaseVideoPlayer',
  props: {
    ball: {
      type: Number,
      default: 1
    },
    video: {
      type: Object,
      default: () => {}
    }
  },
  watch: {
    video (val) {
      if (val.quality) {
        // 
        this.init()
      }
    }
  },
  data () {
    return {
      player: null,
      loading: true
    }
  },
  mounted () {
    // this.init()
  },
  destroyed () {
    this.player.destroy()
  },
  methods: {
    init () {
      this.video.customType = {
        customHls: function (video, player) {
          const hls = new Hls()
          hls.loadSource(video.src)
          hls.attachMedia(video)
        }
      }
      this.player = new DPlayer({
        container: this.$refs.wrap,
        autoplay: false,
        theme: '#FADFA3',
        loop: true,
        lang: 'zh-cn',
        screenshot: true,
        hotkey: true,
        preload: 'auto',
        logo: logo,
        volume: 0.7,
        mutex: true,
        video: this.video
      })
    }
  }
}
</script>

