      z-index: 1;
      color: #999999;
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        margin-right: 20px;
        -webkit-animation: loading 1300ms linear infinite;
        animation: loading 1300ms linear infinite;
      }
      &.hidden {
        display: none;
      }
    }
  }

  @media screen and (max-width: 700px) {
    .base-video {
      width: 100%;
